import MainLayout from "@/components/Layouts/layout";
import ContentHeader from "@/components/Layouts/content-header";
import { ResultCard } from "@/components/Elements/cards/resultCard";
import { searchLeads } from "@/api/leads/leads";
import { useEffect } from "react";
import { store } from "@/Features/Store";
export default function SearchResults(){


    useEffect(() => {
        const currentQueries = store.getState().search;

        searchLeads(currentQueries).then((data) => {
            console.log(data);
        }).catch((error) => {
            console.error(error);
        })

    },[])

    return (
        <MainLayout>
            <ContentHeader title="Search Results"/>
            {/* <ResultCard candidateData={candidateTableData.content}></ResultCard> */}
        </MainLayout>
        
    );
};